@import url("../../styles/defaultSection.css");
@import url("../../styles/variables.css");

.AboutMe {
  color: white;
  background-color: var(--primary-color);
  position: relative;
  display: flex;
  justify-content: space-between;
  /* overflow: hidden; */
}

.AboutMe__text-wrapper {
  /* flex: 1; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: var(--section-padding);
  padding-right: 0;
}

.AboutMe__heading {
  text-transform: uppercase;
  letter-spacing: 10px;
  font-size: clamp(1rem, 2.5vw, 2.5rem);
}

.AboutMe__story {
  font-size: clamp(0.5rem, 2.5vw, 1.25rem);
}

.AboutMe__callToAction {
  text-decoration: underline;
}

.AboutMe__picture {
  height: 123%;
  padding-right: var(--section-padding);

  /* make the picture behave like bottom:0px; in its container. */
  transform: translateY(calc(100% * ((100 / var(--picture-height)) - 1)));
}
